.tooltip-container {
  position: relative;
}

.tooltip-container .tooltip {
  bottom: 100%;
  display: block;
  left: -25px;
  margin-bottom: 15px;
  opacity: 0;
  padding: 1em;
  pointer-events: none;
  position: absolute;
/*  width: 100%; */
    min-width: 20em;
  -webkit-transform: translateY(10px);
     -moz-transform: translateY(10px);
      -ms-transform: translateY(10px);
       -o-transform: translateY(10px);
          transform: translateY(10px);
  -webkit-transition: all .25s ease-out;
     -moz-transition: all .25s ease-out;
      -ms-transition: all .25s ease-out;
       -o-transition: all .25s ease-out;
          transition: all .25s ease-out;
  -webkit-box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.28);
     -moz-box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.28);
      -ms-box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.28);
       -o-box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.28);
          box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.28);
}

.tooltip-container.has-warning .tooltip {
    background: @brand-warning;
}
.tooltip-container.has-error .tooltip {
    background: @brand-danger;
}

/* This bridges the gap so you can mouse into the tooltip without it disappearing */
.tooltip-container .tooltip:before {
  bottom: -20px;
  content: " ";
  display: block;
  height: 20px;
  left: 0;
  position: absolute;
  width: 100%;
}  

/* CSS Triangles - see Trevor's post */
.tooltip-container .tooltip:after {
  border-left: solid transparent 10px;
  border-right: solid transparent 10px;

  bottom: -10px;
  content: " ";
  height: 0;
  left: 50%;
  margin-left: -13px;
  position: absolute;
  width: 0;
}
.tooltip-container.has-warning .tooltip:after {
  border-top: solid @brand-warning 10px;
}
.tooltip-container.has-error .tooltip:after {
  border-top: solid @brand-danger 10px;
}

.tooltip-container:hover .tooltip {
  opacity: 1;
  pointer-events: auto;
  -webkit-transform: translateY(0px);
     -moz-transform: translateY(0px);
      -ms-transform: translateY(0px);
       -o-transform: translateY(0px);
          transform: translateY(0px);
}

/* IE can just show/hide with no transition */
.lte8 .tooltip-container .tooltip {
  display: none;
}

.lte8 .tooltip-container:hover .tooltip {
  display: block;
}